class TestingController {

    constructor(DataService, TestingService, StorageService, $q) {
        'ngInject';

        this.DataService = DataService
        this.TestingService = TestingService
        this.StorageService = StorageService
        this.$q = $q;
    }

    generateData() {
        let deferred = this.$q.defer();
        let promises = [];
        promises.push(this.DataService.generateTestData());
        promises.push(this.DataService.generateQueryData());
        this.$q.all(promises).then(data => {
            this.StorageService.td = data[0];
            this.StorageService.qd = data[1];
            this.identifyKNN();
        });
    }

    identifyKNN() {
        this.TestingService.iterateTestData();
    }
}

export default TestingController;



