import angular from 'angular';

let storageModule = angular.module('StorageService', [])

    .factory('StorageService', storageService).name;

function storageService() {
    let service = {
        td: [],
        qd: [],
        preparedTD: [],
        preparedQD: []
    };
    return service;
}

export default storageModule;


