import angular from 'angular';
import kdbush from 'kdbush';
import rbush from 'rbush';
import knn from 'rbush-knn';
import distance from 'turf-distance';
import async from 'async';

let testingModule = angular.module('TestingService', [])

    .factory('TestingService', testingService).name;

function testingService($q, StorageService, DataService) {
    'ngInject';

    let service = {
        iterateTestData: iterateTestData,
        prepareData: prepareData,
        findNeighbors: findNeighbors
    };
    return service;

    function iterateTestData() {
        let deferred = $q.defer();
        let promises = [];
        promises.push(prepareData(StorageService.td.data, StorageService.preparedTD));
        promises.push(prepareData(StorageService.qd.data, StorageService.preparedQD));
        $q.all(promises).then(function () {
            console.log('Processed: ', StorageService.preparedTD.length, ' test locations');
            console.log('Processed: ', StorageService.preparedQD.length, ' query locations');
            findNeighbors();
        });
    }

    function prepareData(data, dst) {
        var deferred = $q.defer();
        let workerCount = Math.ceil(data.length / 200000);
        let workersFinished = 0;
        while (data.length) {
            let worker = new Worker('./prepare.worker.js');
            worker.postMessage(data.splice(0, 200000));
            worker.onmessage = (evt) => {
                DataService.asyncParse(evt.data).then(data => {
                    DataService.asyncEach(data, entry => {
                        dst.push(entry);
                    }, () => {
                        workersFinished++;
                        worker.terminate();
                        if (workersFinished === workerCount) {
                            deferred.resolve();
                        }
                    }, 100);
                });
            };
        }
        return deferred.promise;
    }

    function findNeighbors() {
        console.log('starting findNeighbors');
        var deferred = $q.defer();
        let workerCount = Math.ceil(StorageService.preparedQD.length / 100000);
        let workersFinished = 0;
        while (StorageService.preparedQD.length) {
            let worker = new Worker('./neighbors.worker.js');
            worker.postMessage(JSON.stringify({
                testData: StorageService.preparedTD,
                queryData: StorageService.preparedQD.splice(0, 100000)
            }));
            worker.onmessage = (evt) => {
                workersFinished++;
                DataService.asyncParse(evt.data).then(data => {
                    console.log('neighbor obj: ', data);
                    worker.terminate();
                    if (workersFinished === workerCount) {
                        deferred.resolve();
                    }
                });
            };
        }
        return deferred.promise;
    }

}

export default testingModule;


